import React, { useState } from 'react';
import Heade from './Heade'; 
import CreateNote from './CreateNote';
import Note from './Note';
import Foote from './Foote';
import './App.css';

function App() {

  const [additem, setitem] = useState([]);

const addNote = (note) => {
  //alert('clicked');
  setitem((prevdata) => {
    return [
      ...prevdata, note
    ]
  });
  console.log(note);
};

const onDelete = (id) => {
  setitem((olddata) => 
  olddata.filter((currdata, index) => {
    return index !== id;
  }));

};

  return (
    <>
    <Heade />
    <CreateNote passNote={addNote} />
    {
      additem.map((val, index) => {
        return <Note 
        key={index}
        id={index}
        title={val.title}
        cotent={val.cotent}
        deleteitem={onDelete}
        />
      })
    }
    <Foote />
    
    </>
  )

}

export default App;
