import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';

const CreateNote = (props) => {

    const [expand, setexpand] = useState(false);

    const [note, setnote] = useState({
        title:'',
        cotent:'',
    });

    const inputevant = (evant) => {

        const {name , value} = evant.target;

        setnote((prevData) => {
            return{
                ...prevData,
                [name]: value,
            };

        });
        console.log(note);
    };

    const addEvant = () => {

        props.passNote(note);
        setnote({
            title:'',
            cotent:'',
        });

    };

    const expandit = () => {
        setexpand(true);
    };

    const backton = () => {
        setexpand(false);
    };

    return(
    <>
    <div className="main_note">
        <form onDoubleClick={backton}>

        {expand ? (  
        <input 
            type="text" 
            name="title"
            value={note.title} 
            onChange={inputevant} 
            placeholder="Title" 
            autoComplete="off"/>
        ) : null
        }


            <textarea 
            rows="" 
            column="" 
            name="cotent"
            value={note.cotent} 
            onChange={inputevant} 
            placeholder="Write a note ..."
            onClick={expandit}
            ></textarea>

            {
            expand ?(
            <Button onClick={addEvant}>
                <AddIcon className="plus_sign"/>

            </Button> ): null
            }
        </form>
    </div>
    
    </>
    )
}
export default CreateNote;